@charset "utf-8";

body{margin: 0; padding: 0;}
$red: #fe0000;
$yellow: #ffd000;
$bkgreen: #1e3829;
$white: #fff;
$gray: #222222;
$black: #000;
$wgray:#808080;
$bggray: rgba(223, 221, 221, 0.2);
$fs50:50px;
$fs40:40px;
$fs20:20px;
$fs18:18px;
$fs16:16px;
$fs15:15px;
$fs13:13px;
$pd15:5px 15px;


@mixin p16 {
    p{
        font-size: 16px; color:$wgray;
    }
}

.container{width:1170px; height: inherit; background: grba(0,0,0,.4); margin: 0 auto;}


#header{width:100%; height: 140px; text-align: center; border-bottom: 1px solid $wgray; box-sizing: border-box; background: $white;position: fixed; top: 0; left: 0; right: 0;
    .logo{
        float: left; margin-top: 25px;
    }
    .menu-top > li{
         float: left;  margin-right: 85px; position: relative; left: 5%; margin-top: 50px;
         &:hover{border-bottom: 4px solid $yellow;}
        >a{font-size: $fs20; font-weight: bold;
        }
        >ul{
            width:152px; height: 300px; position: absolute; left:12%; top:100px; text-align: left; z-index: 0;
            >li{
                margin-bottom: 30px; font-size: $fs16; display: none;
            a{
                color:$wgray;
                    &:hover{color:$black; text-decoration: underline;}
                } 
            }
        }
    }
    .right-menu > li{
        float:left; margin-top: 50px; margin-right: 15px; position: relative; left: 5%; 
        .red{border: 1px solid $red; font-size: $fs13; background: $red; padding: $pd15; border-radius: 15px;}
        .yellow{border: 1px solid $yellow; font-size: $fs13; background: $yellow;  padding: $pd15; border-radius: 15px;}
        .green{border: 1px solid $bkgreen; font-size: $fs13; background: $bkgreen;  padding: 5px 25px; border-radius: 15px;}
        >a{color:$white;}
    }
    .search{position: relative; left: -8px; top:40px}
}
#content1{
    background-image: url(../img/bg_visual_story01.jpg);width:100%; height: 220px; margin-top:120px;
.top-content{ color:$white;
    h1{font-size: $fs50;}
    p{font-size: $fs18;}
    ul > li{float: left; margin-right: 30px; font-size: $fs16; margin-top: 5px; font-weight: bold;
        a{color: $white;}
        }
    }
}

#content2{ padding-bottom: 350px; border-bottom: 1px solid $wgray;
    text-align: center;
            ul {display: inline-block; margin: 30px 0;
                 li{
            float: left; margin-right: 20px; font-size: $fs20;
                a{
                    display: inline-block;
                    }
            }
        }
        h2{font-size: 30px; float: left; margin: 30px 0; font-weight: bold;}
        p{float: left; text-align: left;}
        @include p16;
    }
#content3{
    margin-top:100px;
    h2{ font-size: 30px;}
}